$pink: #f26b6b;
$pink-bg-vibrant: #E97676;
$pink-bg: rgba(255,90,90,8%);

.bg-pink {
    background-color: $pink-bg-vibrant;
}

.bg-lightpink {
    background-color: $pink-bg;
}


h2, .pink {
    color: $pink;
}

h4 {
    color: #7a7a7a;
}

.cover-container {
    background-color: #E97676;
    height: 574px;
    width: 100vw;
    overflow: hidden;
}

.cover {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 0;
}

.threegrid-box {
        background-color: $pink-bg;
        h4 {
            color: $pink;
        }
    }

.project-quote {
    background-color: $pink-bg;
}

.big-numbers {
    color: #ffc0c0;
}

.big-descriptions {
    color: $pink;
}

.onleft-title {
    color: $pink;
}